<template>
  <!--  -->
  <el-dialog title="" :visible.sync="_dialogVisible" width="830px">
    <!--  -->
    <div class="table-search">
      <div class="search-form">
        <el-input v-model="searchValue" size="small">
          <el-button slot="append" icon="el-icon-search" @click="getList" />
        </el-input>
      </div>
      <div class="search-btn">
        <el-button size="small" @click="getList">
          
        </el-button>
      </div>
    </div>
    <!--  -->
    <el-table
      v-loading="loading"
      class="dialog-table"
      :data="list"
      style="width: 100%"
      @selection-change="handleSelectionChange"
    >
      <el-table-column v-if="selectType === 'multi'" type="selection" width="55" />
      <el-table-column label="" prop="jvCode" />
      <el-table-column label="" prop="jvName">
        <!-- <template slot-scope="scope">
          <div class="flex-center pr-20">
            <el-radio
              v-if="selectType === 'single'"
              v-model="checkedId"
              :label="scope.row.id"
              class="mr-0"
            >
              <span />
            </el-radio>
            <img :src="scope.row.img" width="50" height="50"
                 class="mr-10"
            >
            <p class="product-name">
              {{ scope.row.name }}
            </p>
          </div>
        </template> -->
      </el-table-column>
      <el-table-column label="" prop="brand" />
    </el-table>
    <!--  -->
    <div class="table-page">
      <el-pagination
        background
        :current-page="nowPage"
        layout="total, prev, pager, next, jumper"
        :total="total"
        @current-change="pageChange"
      />
    </div>
    <!--  -->
    <div slot="footer" class="dialog-footer">
      <el-button size="medium" type="primary" @click="selectSure">
         
      </el-button>
      <el-button size="medium" @click="_dialogVisible = false">
         
      </el-button>
    </div>
  </el-dialog>
</template>

<script>
import { fetchProduct } from '@/api/index'

export default {
  name: 'DialogProduct',
  props: {
    // 
    dialogVisible: {
      type: Boolean,
      default: false
    },
    //  -   single- multi-
    selectType: {
      type: String,
      default: 'single'
    }
  },
  data() {
    return {
      loading: false, // 
      searchValue: '', // 
      nowPage: 1, // 
      total: 0, // 
      // 
      list: [],
      checkedId: '', // ID
      multipleSelection: [] // 
    }
  },
  computed: {
    // 
    _dialogVisible: {
      get: function() {
        return this.dialogVisible
      },
      set: function(val) {
        this.$emit('update:dialogVisible', val)
      }
    }
  },
  watch: {
    // 
    dialogVisible(newVal, oldVal) {
      if (newVal) {
        this.list = []
        this.nowPage = 1
        this.getList()
      }
    }
  },
  mounted() {

  },
  methods: {
    // 
    pageChange(val) {
      this.nowPage = val
      this.getList()
    },
    // 
    getList() {
      var qm = {
        jvStatus: 1,
        name: this.searchValue,
        pageNum: this.nowPage,
        pageSize: 10,
        skuType: 0,
        status: 1
      }
      this.loading = true
      fetchProduct(qm).then(res => {
        this.loading = false
        this.list = res.list
        this.total = res.total
      })
    },
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    // 
    selectSure() {
      // 
      if (this.selectType === 'single') {
        if (!this.checkedId) {
          this.$message.error('')
          return
        }
        let item = this.list.find(item => item.id === this.checkedId)
        this.$emit('selectSure', item)
      } else {
        // 
        if (this.multipleSelection.length === 0) {
          this.$message.error('')
          return
        }
        this.$emit('selectSure', this.multipleSelection)
      }
      this._dialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.product-name {
  .ellipsis;
}
</style>
