import {productListApi} from '@/api/link'

export default [
  {
    clickType: 1,
    label: '',
    component: 'com-table',
    url: productListApi,
    linkTextAttr: 'name',
    query: {
      jvStatus: 1,
      skuType: 0,
      status: 1
    },
    saveKeys: ['id', 'name'],
    columns: [
      { label: '', key: 'jvCode' },
      { label: '', key: 'name' },
      { label: '', key: 'brand' }
    ],
    formConfig: {
      formAttrMap: {
        labelWidth: '110px'
      },
      formItemMap: {
        jvCode: {
          label: '',
          placeholder: '',
          value: null
        },
        name: {
          label: '',
          placeholder: '',
          value: null
        },
        brand: {
          label: '',
          placeholder: '',
          value: null
        }
      }
    }
  },
  {
    clickType: 2,
    label: 'url',
    linkTextAttr: 'redirectUrl',
    component: 'inner-url'
  },
  {
    clickType: 3,
    label: 'url',
    linkTextAttr: 'redirectOuterUrl',
    component: 'outer-url'
  }
]
