<template>
  <div>
    <draggable
      v-model="picData"
      :options="{
        animation: 300,
        handle: '.up-pic-item',
        disabled: unGraggable || picData.length < 2,
        forceFallback: true,
        fallbackTolerance: 1,
      }"
    >
      <!--  -->
      <div
        v-for="(item, index) in picData"
        :key="index"
        class="up-pic-item"
        :class="{ 'can-drag': !unGraggable && picData.length > 1 }"
      >
        <div class="up-pic-item-wrapper">
          <UpLoadBox
            v-if="showPic"
            :img-url.sync="item.imageUrl"
            @editImg="editPic(item)"
          />
          <div class="info">
            <dl v-if="showName" class="com-form-group">
              <dt class="form-label">
                
              </dt>
              <dd class="form-container">
                <el-input
                  v-model.lazy="item.text"
                  class="input-name"
                  :placeholder="inputPlaceHolder"
                />
              </dd>
            </dl>
            <dl class="com-form-group">
              <dt class="form-label">
                
              </dt>
              <dd class="form-container">
                <!--  -->
                <ConfigLink :link-obj.sync="item.link" />
              </dd>
            </dl>
          </div>
        </div>
        <div
          v-if="showDelete"
          class="up-pic-item-delete el-icon-error"
          title=""
          @click="deleteItem(index)"
        />
      </div>
    </draggable>
    <!--  -->
    <ButtonAdd v-if="showAdd" :add-text="addPlaceHolder" @click="addItem" />
  </div>
</template>

<script>
import draggable from 'vuedraggable' // 
import UpLoadBox from '@/components/BasicUi/UpLoadBox'
import ButtonAdd from '@/components/BasicUi/ButtonAdd'
import ConfigLink from '@/components/BasicConfig/ConfigLink'

export default {
  name: 'PicList',
  components: {
    draggable,
    UpLoadBox,
    ButtonAdd,
    ConfigLink
  },
  props: {
    // 
    imageList: {
      type: Array,
      default: null
    },
    // 
    inputPlaceHolder: {
      type: String,
      default: ''
    },
    // 
    addPlaceHolder: {
      type: String,
      default: ''
    },
    // 
    showPic: {
      type: Boolean,
      default: true
    },
    // 
    showName: {
      type: Boolean,
      default: true
    },
    // 
    showAdd: {
      type: Boolean,
      default: true
    },
    // 
    showDelete: {
      type: Boolean,
      default: true
    },
    // 
    unGraggable: {
      type: Boolean,
      default: false
    },
    // 
    limitSize: {
      type: Number,
      default: 10
    }
  },
  data() {
    return {
      // 
      picData: JSON.parse(JSON.stringify(this.imageList)),
      currentItem: '' // -  
    }
  },
  watch: {
    // 
    picData: {
      handler: function (newVal, oldVal) {
        console.log('', newVal)
        this.$emit('update:imageList', newVal)
      },
      deep: true
    }
  },
  methods: {
    // 
    addItem() {
      // 
      if (this.limitSize && this.picData.length >= this.limitSize) {
        this.$message.warning(`${this.limitSize}`)
        return
      }
      // 
      if (this.showPic) {
        this.showDialogImage()
        this.currentItem = ''
      } else {
        // 
        let item = {
          link: null, // 
          imageUrl: '', // 
          text: '' // 
        }
        this.picData.push(item)
      }
    },
    // 
    editPic(item) {
      this.showDialogImage()
      this.currentItem = item
    },
    // 
    deleteItem(index) {
      this.picData.splice(index, 1)
    },
    // 
    showDialogImage() {
      this.$store.commit('SET_UPIMAGE_VISIBLE', true)
      this.$store.commit('SET_UPIMAGE_FUC', this.upLoadImgSuccess)
    },
    // 
    upLoadImgSuccess(imgUrl) {
      if (this.currentItem) {
        // 
        this.currentItem.imageUrl = imgUrl
      } else {
        // 
        let item = {
          link: null, // 
          imageUrl: imgUrl, // 
          text: '' // 
        }
        this.picData.push(item)
      }
    }
  }
}
</script>

<style lang="less" scoped>
// 
.up-pic-item {
  position: relative;
  margin-bottom: 12px;
  padding: 6px 0;
  background: #ffffff;
  box-shadow: 0 0 4px 0 rgba(10, 42, 97, 0.2);
  border-radius: 2px;
  user-select: none;
  &.can-drag {
    cursor: move;
  }
  &.sortable-ghost {
    opacity: 0.2;
  }
  &:hover {
    .up-pic-item-delete {
      visibility: visible;
    }
  }
  /deep/.config-link .cllt-name {
    max-width: 110px;
  }
}
.up-pic-item-wrapper {
  display: flex;
  .up-pic-box {
    margin: 6px 0 0 12px;
  }
  .info {
    flex: 1;
  }
}
// 
.com-form-group {
  padding: 6px 12px;
  // display: flex;
  align-items: center;
  .form-label {
    margin-right: 16px;
    font-size: 14px;
    color: #323233;
    line-height: 18px;
    white-space: nowrap;
  }
  .form-container {
    flex: 1;
  }
  /deep/ .input-name .el-input__inner {
    height: 32px;
    line-height: 32px;
    padding: 0 10px;
    border-radius: 2px;
  }
}
</style>
