<template>
  <div>
    <!--  -->
    <ComGroup title="" :select-value="selectLayout.name">
      <ComRadioGroup v-model="configData.layout" :radio-list="layoutList" />
    </ComGroup>

    <!--  -->
    <ComDivider />
    <ComGroup title="">
      <!-- <el-input-number v-model="configData.borderRadius"></el-input-number> -->
      <el-slider
        v-model="configData.borderRadius"
        show-input
        :show-input-controls="false"
        input-size="mini"
      />
    </ComGroup>
    <!--  -->
    <ComGroup title="">
      <el-radio v-model="paddingDefault" :label="true">
        
      </el-radio>
      <el-radio v-model="paddingDefault" :label="false">
        
      </el-radio>
    </ComGroup>
    <div v-if="!paddingDefault">
      <ComGroup title="">
        <el-slider
          v-model="configData.columnPadding"
          show-input
          :show-input-controls="false"
          input-size="mini"
        />
      </ComGroup>
      <ComGroup title="">
        <el-slider
          v-model="configData.rowPadding"
          show-input
          :show-input-controls="false"
          input-size="mini"
        />
      </ComGroup>
    </div>
    <ComGroup title="">
      <el-slider
        v-model="configData.lineNumber"
        show-input
        :show-input-controls="false"
        input-size="mini"
        :max="8"
        :min="1"
      />
    </ComGroup>
    <!--  -->
    <ComGroup
      title=""
      tips=" 10 "
      :name-black="true"
    />

    <ComGroup :content-block="true">
      <PicList
        :image-list.sync="configData.imageList"
        add-place-holder=""
        :show-pic="selectLayout.id === 'pic'"
      />
    </ComGroup>

    <!--  -->
    <ComGroup title="">
      <el-button
        type="text"
        class="mr-15"
        @click="configData.backgroundColor = initBgColor"
      >
        
      </el-button>
      <el-color-picker v-model="configData.backgroundColor" size="small" />
    </ComGroup>
    <ComGroup title="">
      <el-button
        type="text"
        class="mr-15"
        @click="configData.textColor = initTxtColor"
      >
        
      </el-button>
      <el-color-picker v-model="configData.textColor" size="small" />
    </ComGroup>
  </div>
</template>

<script>
import ComGroup from '@/components/BasicUi/ComGroup'
import ComRadioGroup from '@/components/BasicUi/ComRadioGroup'
import ComDivider from '@/components/BasicUi/ComDivider'
import PicList from '@/components/BasicConfig/PicList'

export default {
  name: 'Navigation',
  components: { ComGroup, ComRadioGroup, ComDivider, PicList },
  props: ['parmes'],
  data() {
    return {
      // 
      configData: JSON.parse(JSON.stringify(this.parmes)),
      // 
      initBgColor: '#FFFFFF',
      // 
      initTxtColor: '#323233',
      // 
      layoutList: [
        {
          name: '',
          id: 'pic',
          icon: 'up2end'
        }
      ],
      // 
      paddingDefault: true
    }
  },
  computed: {
    // 
    selectLayout() {
      return this.layoutList.find((item) => item.id === this.configData.layout)
    }
  },
  watch: {
    // 
    configData: {
      handler: function (newVal, oldVal) {
        console.log('', newVal)
        // 
        this.$emit('editComponent', newVal)
      },
      deep: true
    }
  }
}
</script>
