export const isEmpty = val => {
  return val === '' || val === undefined || val === null
}

/**
 * @name: 
 * @return {Object} Vuecomponents
 */
export function dynamicComponents() {
  let components = {}
  const dycomponent = require.context('@/components/ComponentsConfig', true, /\.vue$/)
  dycomponent.keys().map(item => {
    // 
    const name = item.split('/')[1]
    components[name] = dycomponent(item).default
  })
  return components
}

/**
 * @name: H5
 * @param {Window} win  
 * @param {String} targetOrigin  winorigin
 */
export class Messager {
  // 
  constructor(targetOrigin) {
    this.targetOrigin = targetOrigin
    this.actions = {}
    // 
    this.messageListener = (event) => {
      // 
      const type = event.data && event.data.type
      if (event.origin === this.targetOrigin && type && this.actions[type]) {
        this.actions[type](event.data.value)
      }
    }
    window.addEventListener('message', this.messageListener)
  }
  /**
   * @name: 
   * @param {String} type  
   * @param {Function} cb  
   */
  on(type, cb) {
    this.actions[type] = cb
    return this
  }
  /**
   * @name: 
   * @param {String} type   
   * @param {Object} value  
   */
  emit(type, value) {
    var win = document.getElementById('previewIframe').contentWindow
    win.postMessage({
      type, value
    }, this.targetOrigin)
    return this
  }
  // 
  destroy() {
    window.removeEventListener('message', this.messageListener)
  }
}

/**
 * 
 * @param  {Function}     func     
 * @param  {Number}       wait     
 * @param  {Boolean}      immediate     
 * @return {Function}              
 */
export function debounce(func, wait = 500, immediate) {
  let timer
  return function () {
    let context = this
    let arg = arguments

    if (immediate) {
      func.apply(context, arg)
      immediate = false
      return
    }

    if (timer) clearTimeout(timer)
    timer = setTimeout(() => {
      func.apply(context, arg)
    }, wait)
  }
}

/**
 * 
 * @param  {Function}     func     
 * @param  {Number}       wait     
 * @param  {Boolean}      immediate     
 * @return {Function}              
 */
export function throttle(func, wait = 500, immediate) {
  let timer
  return function () {
    let context = this
    let arg = arguments

    if (immediate) {
      func.apply(context, arg)
      immediate = false
      return
    }

    if (timer) return
    timer = setTimeout(() => {
      func.apply(context, arg)
      timer = null
    }, wait)
  }
}

/**
 * id 
 */
export function createRandomId() {
  return Number(Math.random().toString().substr(3, 5) + Date.now()).toString(36)
}
