<template>
  <div>
    <!--  -->
    <ComGroup title="">
      <el-button type="text" class="mr-15" @click="configData.backgroundColor = initBgColor">
        
      </el-button>
      <el-color-picker v-model="configData.backgroundColor" size="small" />
    </ComGroup>
    <ComGroup title="" :content-block="true">
      <quill-editor ref="myTextEditor" v-model="configData.content" :options="editorOption" />
      <Uploader
        ref="quillUploader"
        class="avatar-uploader"
        :show-file-list="false"
        list-type="picture"
        :max-size="upload.maxSize"
        :limit="upload.limit"
        :on-change="onChangeQuill"
      />
    </ComGroup>
  </div>
</template>

<script>
import ComGroup from '@/components/BasicUi/ComGroup'

export default {
  name: 'RichText',
  components: { ComGroup },
  props: ['parmes'],
  data() {
    return {
      // 
      configData: JSON.parse(JSON.stringify(this.parmes)),
      // 
      initBgColor: '',
      editorOption: {
        placeholder: '',
        modules: {
          toolbar: {
            container: [
              ['bold', 'italic', 'underline', 'strike'], // 
              // ['blockquote', 'code-block'], // 
              // [{ 'header': 1 }, { 'header': 2 }], // 12
              [{ 'list': 'ordered' }, { 'list': 'bullet' }], // 
              // [{ 'script': 'sub' }, { 'script': 'super' }], // 
              // [{ 'indent': '-1' }, { 'indent': '+1' }], // 
              [{ 'direction': 'rtl' }], // 
              ['clean'], // 
              // [{ 'size': ['small', false, 'large', 'huge'] }], // 
              [{ 'header': [1, 2, 3, 4, 5, 6, false] }], // 
              [{ 'align': [] }], // 
              [{ 'color': [] }, { 'background': [] }], // 
              ['image'] // linkvideo
            ],
            handlers: {
              image: function(value) {
                if (value) {
                  // 
                  document.querySelector('.avatar-uploader input').click()
                } else {
                  this.quill.format('image', false)
                }
              }
            }
          }
        }
      },
      upload: {
        maxSize: 1,
        limit: 99
      }
    }
  },
  watch: {
    // 
    configData: {
      handler: function(newVal, oldVal) {
        console.log('', newVal)
        // 
        this.$emit('editComponent', newVal)
      },
      deep: true
    }
  },
  methods: {
    // 
    onChangeQuill(file, fileList) {
      if (file.status !== 'finished') return
      let quill = this.$refs.myTextEditor.quill
      let length = quill.getSelection().index
      // 
      let imgSrc = file.response.data
      quill.insertEmbed(length, 'image', imgSrc)
      // 
      quill.setSelection(length + 1)
    }
  }
}
</script>

<style lang="less" scoped>
/deep/.ql-container.ql-snow {
  height: 200px;
}
.avatar-uploader {
  display: none !important;
}
</style>
